<template>
    <div class="s-l-container" :style="slContainerStyles">
        <slot></slot>
    </div>
</template>

<script>
    const WINDOW_HEIGHT = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
    export default {
        data(){
            return {
                screenHeight: 0
            }
        },
        computed: {
            slContainerStyles() {
                return {
                    height: this.screenHeight + 'px'
                }
            }
        },
        mounted() {
            this.$nextTick(() => this.screenHeight = WINDOW_HEIGHT)
            window.addEventListener('resize', () => this.screenHeight = WINDOW_HEIGHT)
        }
    }
</script>

<style scoped lang="less">
@import '../style/components/screen_layout.less';
</style>


